
.home .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.hero {
  /* Sizing */
  width: 100vw;
  height: 100vh;

  /* Background styles */
  background-image: url(https://images.unsplash.com/photo-1517836357463-d25dfeac3438?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.top h1 {
  margin-top: 3em;
  font-size: 72px;
  color: white;
  line-height: 1.1em;
}

.top button {
  color: black;
  background-color: orange;
  border-color: orange;
  font-size: 1.2em;
  width: 20%;
  padding: 2% 1% 2% 1%;
  font-weight: 600;
  margin-top: 4%;
}

.top button:hover {
  opacity: 0.8;
}

.home-middle {
  margin-top: 26%;
  margin-bottom: 26%;
}

.membership {
  font-size: 5em;
  font-weight: 700;
}
.first-p {
  font-size: 1.8em;
  font-weight: 600;
}

.unlimited,
.drop-in {
  margin-top: 20%;
  font-size: 1.25em;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
}

.second-middle {
  height: 50vh;
  background-image: url(https://images.unsplash.com/photo-1547226238-e53e98a8e59d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.second-middle h1 {
  color: white;
  font-size: 72px;
  font-weight: 700;
  text-shadow: -1px 4px 0px rgba(4, 4, 4, 0.25);
}

.group {
  padding: 15% 100px 15% 100px;
}

.learn-more {
  padding-top: 2%;
}
.learn-more a {
  color: orange;
  border-bottom: 0.5px solid orange;
  font-size: 0.9em;
  letter-spacing: 1.5px;
  text-shadow: -1px 4px 0px rgba(4, 4, 4, 0.25);
}

.learn-more a:hover {
  opacity: 0.6;
  text-decoration: none;
  color: orange;
}

.quote {
  padding-left: 0;
  margin-top: 26%;
  margin-bottom: 26%;
}

.quote h1 {
  font-weight: 700;
  font-size: 72px;
  padding-right: 35%;
}

.quote p {
  padding-top: 4%;
  font-size: 1.6em;
  font-weight: 700;
}

.home-trainer {
  height: 50vh;
  background-image: url(https://images.unsplash.com/photo-1519311965067-36d3e5f33d39?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1051&q=80);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  background-attachment: scroll;
}

.home-trainer h1 {
  color: white;
  padding-right: 60%;
  font-size: 4em;
  font-weight: bold;
  text-shadow: -1px 4px 0px rgba(4, 4, 4, 0.25);
}

.main .icon {
  color: white;
  font-weight: 600;
  font-size: 1.8em;
}
.main .icon-2 {
  color: black;
  font-weight: 600;
  font-size: 1.8em;
}

.main .burger-border {
  border-color: white;
}

.main .burger-border-2 {
  border-color: black;
}

.burger {
  width: 35px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
}
.burger-2 {
  width: 35px;
  height: 3px;
  background-color: black;
  margin: 6px 0;
}

.main .link-style {
  color: white;
  font-weight: 600;
  font-size: 1.1em;
}
.main .link-style:hover {
  text-decoration: none;
  border-bottom: 2px solid orange;
}

.main .link-style-2 {
  color: black;
  font-weight: 600;
  font-size: 1.1em;
}
.main .link-style-2:hover {
  text-decoration: none;
  border-bottom: 2px solid orange;
}

#onPage {
  border-bottom: 2px solid orange;
}

.footer {
  padding-top: 10%;
  padding-bottom: 15%;
  background-color: #f6f6f6;
}

.footer .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.footer h1 {
  font-size: 4em;
  font-weight: bold;
}

.footer p {
  padding-top: 4%;
  font-size: 1.5em;
  font-weight: 500;
}

.footer .btn {
  color: black;
  background-color: orange;
  border-color: orange;
  font-size: 1.2em;
  width: 50%;
  padding: 2% 1% 2% 1%;
  font-weight: 600;
  margin-left: 50%;
}

.footer .btn:hover {
  opacity: 0.8;
}

.copyright {
  color: rgb(70, 70, 70);
  font-size: 1.2em;
  font-weight: bold;
  padding-top: 7%;
}

.join-middle {
  margin-top: 6%;
  margin-bottom: 6%;
}

.join {
  font-size: 4.5em;
  font-weight: 700;
}
.join-first-p {
  font-size: 1.5em;
  font-weight: 600;
}

.join-unlimited,
.join-drop-in {
  margin-top: 20%;
  font-size: 1.1em;
  font-family: "Roboto", sans-serif;
}

.join-form {
  margin-top: 5%;
  letter-spacing: 1px;
  font-size: 0.86em;
}

.join-form input {
  background: #f6f6f6;
}

.join-form .form-btn {
  border: 1px solid black;
  color: black;
  padding: 2% 6% 2% 6%;
}

.join-form .form-btn:hover {
  color: white;
  background-color: black;
}

.join-main .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.custom-img img {
  object-fit: cover;
  object-position: top;
  border: 1px solid red;
  height: 40vh;
  width: 100%;
}

.carousel-pics {
  margin-top: 15%;
  height: 60vh;
  width: 100%;
}

.carousel-pics img {
  height: 60vh;
  width: 100%;
  object-fit: cover;
  object-position: top;
}

.about-unlimited,
.about-drop-in {
  margin-top: 10%;
  font-size: 1.05em;
  letter-spacing: 0.5px;
  font-family: "Roboto", sans-serif;
}

.about-bottom {
  margin-top: 13%;
  margin-bottom: 13%;
}

.about .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.class-list {
  margin-top: 15%;
}

.space {
  margin-bottom: 10%;
}

.class-list h4 {
  font-size: 1.3em;
  font-family: "Roboto", sans-serif;
}

.class-list h5 {
  margin-top: 12%;
  font-size: 1em;
  font-family: "Roboto", sans-serif;
}

.trainer .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.margin {
  margin-top: 12%;
}

.trainer .trainer-card {
  margin-bottom: 10%;
}

.trainer-header img {
  width: 100%;
  height: 25em;
  object-fit: cover;
}

.trainer-body h5 {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  letter-spacing: 0.5px;
}
.trainer-body h5:first-child {
  margin-top: 10%;
  margin-bottom: 6%;
}

.trainer-body p {
  letter-spacing: 0.5px;
}

.item .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.item-main {
  margin-top: 10%;
  margin-bottom: 8%;
}

.item-body img {
  height: 20em;
  width: 100%;
  object-fit: cover;
}

.right {
  padding-right: 30%;
}

.right h2 {
  margin-bottom: 15%;
}

.right span {
  font-size: 1.3em;

  color: orange;
}

.right ul {
  margin-top: 12%;
  margin-bottom: 12%;
}

.right .btn {
  background-color: orange;
  color: black;
  font-weight: bold;
  width: 60%;
  margin-top: 10%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.item-form {
  margin-top: 12%;
}

.item-form select {
  border: 2px solid black;
  height: 2.5em;
  font-size: 1.2em;
}

.item-form input[type="number"] {
  border: 2px solid black;
  padding: 7%;
  width: 50%;
  font-size: 1.2em;
}

.store .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.store-card {
  position: relative;
  width: 100%;
  padding-top: 2%;
}

.image {
  display: block;
  width: 100%;
  height: 30vh;
  object-fit: cover;
}

.overlay {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: black;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
  opacity: 0.8;
}

.store-card:hover .overlay {
  bottom: 0;
  height: 100%;
}

.text {
  color: white;
  font-size: 1.3em;
  font-family: "Roboto", sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  letter-spacing: 1px;
}

.store-price {
  font-size: 0.8em;
  color: orange;
}

.store-item {
  margin-top: 13%;
  margin-bottom: 10%;
}

.pad {
  padding-bottom: 30px;
}

.cart .container-fluid {
  padding-left: 100px;
  padding-right: 100px;
}

.cart-main {
  margin-top: 10%;
  margin-bottom: 10%;
}

.cart-main h3 {
  margin-bottom: 3%;
}

tr {
  font-size: 0.8em;
  letter-spacing: 1px;
}

th img {
  width: 40%;
  object-fit: scale-down;
  height: 20%;
}

th input {
  width: 18%;
  text-align: center;
}

.cart tr .line-up {
  padding-top: 6%;
}

.checkout {
  text-align: right;
}

.checkout p {
  font-size: 0.8em;
  letter-spacing: 1px;
}

.checkout .btn {
  border: 1px solid black;
}

.checkout .btn:hover {
  color: white;
  background-color: black;
}

html body {
  font-family: "PT Sans", sans-serif;
}

.app {
  overflow: hidden;
}

