  }
  .pad {
    padding-bottom: 0;
  }
  .main a {
    font-size: 1.3em;
  }
  .main .icon {
    font-size: 2.5em;
  }
  .item .container-fluid {
    padding-left: 10px;
    padding-right: 10px;
  }
  .right {
    padding-right: 20%;
  }
  .img-thumbnail {
    display: none;
  }
}

@media screen and (min-width: 600px) {
  .home .container-fluid {
    padding-left: 50px;
    padding-right: 50px;
  }
  .membership {
    font-size: 3.5em;
  }
  .first-p {
    font-size: 1.5em;
  }
  .unlimited,
  .drop-in {
    font-size: 1.3em;
  }
